import React, { useEffect, useState } from 'react';
import { Group, Paper, Text, useMantineTheme, SimpleGrid } from '@mantine/core';
import {
  IconUserPlus,
  IconDiscount2,
  IconReceipt2,
  IconCoin,
  IconArrowUpRight,
  IconArrowDownRight,
} from '@tabler/icons-react';
import useStyles from '../../styles/useStyles';
import data from '../../data/data.json'
import axios from 'axios';

const TopStats = () => {
  const { classes } = useStyles();
  const [visible, setVisible] = useState(false);
  const [totalProfit, setTotalProfit] = useState(0);
  const [investments, setInvestments] = useState(0);
  const [openTrades, setOpenTrades] = useState(0);
  const [closedTrades, setClosedTrades] = useState(0);
  const theme = useMantineTheme();

  const icons = {
    user: IconUserPlus,
    discount: IconDiscount2,
    receipt: IconReceipt2,
    coin: IconCoin,
  };



  useEffect(() => {
     const getAllTrades = async()=>{
      return axios.get('/allTrades');
     }
     getAllTrades().then((resp)=>{
      console.log(resp.data.allTrades);
      const trades = resp.data.allTrades;
      let totalProfit=0;
      let netInvestment=0;
      let openTrades=0;
      let closedTrades=0;
      trades.forEach(item => { 
        netInvestment+= 0.1;  
        if(item.sellAtTime === null){
          openTrades++;
        } else {
          closedTrades++;
          const qtty = 0.1/item.buyAtPrice; 
          const afterSale = qtty*item.sellAtPrice; 
          totalProfit+= afterSale-0.1;
        } 
      }); 
      setTotalProfit(totalProfit);
      setInvestments(netInvestment);
      setOpenTrades(openTrades);
      setClosedTrades(closedTrades)
     })
  },[false])

  return (
    <div className={classes.root}>
      <SimpleGrid cols={4} breakpoints={[{ maxWidth: 'sm', cols: 1 }]}>
        <Paper withBorder p="md" radius="md" key="totalProfit">
          <Group position="apart">
            <Text size="xs"  fw="900">
              Net Profit
            </Text>
            <IconCoin className={classes.icon} size="1.4rem" stroke={1.5} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={5}>
            <Text color={totalProfit > 0 ? 'lime' : 'red'} fz="md" fw="900">
              {Number(totalProfit).toFixed(4)} (BNB)
            </Text>
          </Group>
        </Paper>
        <Paper withBorder p="md" radius="md" key="totalInv" >
          <Group position="apart">
            <Text size="xs" fw="900">
              Net Investments
            </Text>
            <IconUserPlus className={classes.icon} size="1.4rem" stroke={1.5} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={5}>
          <Text  fz="md" fw="900" color={investments > 0 ? 'lime' : 'red'}>
          {investments && investments.toFixed(4)} (BNB)
          </Text>
          </Group>
        </Paper>
        <Paper withBorder p="md" radius="md" key="openTrades" >
          <Group position="apart">
            <Text size="xs" fw="900">
              Open Trades
            </Text>
            <IconReceipt2 className={classes.icon} size="1.4rem" stroke={1.5} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={5}>
          <Text  fz="md" fw="900">
          { openTrades}
            </Text>
          </Group>
        </Paper>
        <Paper withBorder p="md" radius="md" key="closed" >
          <Group position="apart">
            <Text size="xs" fw="900">
              Closed Trades
            </Text>
            <IconDiscount2 className={classes.icon} size="1.4rem" stroke={1.5} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={5}>
            <Text  fz="md" fw="900">
            { closedTrades}
            </Text>
          </Group>
        </Paper>
      </SimpleGrid>
    </div>
  );
}

export default TopStats;