import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import AppHeader from './components/AppHeader/AppHeader';
import NavItems from './components/Nav/NavItems';
import { Grid, Col, Title, Text, Button, Container, Card, Input, Group, NumberInput, TextInput } from '@mantine/core';
import useStyles from './styles/useStyles';
import dayjs from 'dayjs';
import {
  Avatar,
  Badge,
  Table,  
  ActionIcon,
  Anchor,
  ScrollArea,
  useMantineTheme,
} from '@mantine/core';
import { IconDots, IconPencil, IconEye, IconTrash } from '@tabler/icons';
import { LoadingOverlay } from '@mantine/core'; 
import axios from 'axios';
import TopStats from './components/dashboardstats/TopStats';
const  App=()=> {
  const { classes } = useStyles();
  const theme = useMantineTheme();

  const [totalProfit, setTotalProfit] = useState(0);
  const [visible, setVisible] = useState(false);  
  const [allTrades, setAllTrades] = useState([]); 
  const jobColors = {
    open: 'blue',
    closed: 'cyan', 
  };
  const getTradeRows = (rows) =>  {
    return rows.map((item) => (
    <tr key={item.name}>
      <td>
        <Group spacing="sm"> 
          <Text fz="md" fw={800}>
           <a href={`https://dexscreener.com/bsc/${item.pairAddress}`} style={{color:'#ffdd11'}}>  {item.name} </a>
          </Text>
        </Group>
      </td>
      <td>
        <Group spacing="sm"> 
          <Text fz="sm" fw={500}>
            {item.buyAtPrice}
          </Text>
        </Group>
      </td>
      <td>
        <Group spacing="sm"> 
          <Text fz="sm" fw={500}>
            {item.sellAtPrice}
          </Text>
        </Group>
      </td>
      <td>
        <Badge radius='0'
          color="ocean-blue" variant="filled"
        >
          {item.investment} (BNB)
          
        </Badge>
      </td>
      <td>
        <Badge
          color="gray" variant="filled" radius='0'
        >
          {item.quantity} {item.name}
        </Badge>
      </td>
      <td>
        <Badge radius='0' component="button" size="md">
          {new Date(Date.parse(item.signaledAt)).toLocaleString()}
        </Badge>
      </td>
      <td> 
      <Badge radius='0'
          color={item.profit>0 ?"lime":"red"} variant="filled" sx={{fontSize:"16px"}}>
          {(0.01* item.investment * (item.profit)).toFixed(3)} BNB
        </Badge> 
      </td>
      <td>
        <Group spacing={0} position="right"> 
          <ActionIcon color="red" onClick={()=>deleteTrade(item.id)}>
             <IconTrash size="1rem" stroke={1.5} /> 
          </ActionIcon>
        </Group>
      </td>
    </tr>
  ))};

  const refreshData=()=>{
    const getAllTrades = async()=>{
      return axios.get('/allTrades');
     }

     getAllTrades().then((resp)=>{
      console.log(resp.data.allTrades);


      let trades = resp.data.allTrades; 
      let totalProfit=0;
      let netInvestment=0;
      let openTrades=0;
      let closedTrades=0;
      trades.forEach(async item => { 


        const dexscreener = await axios
        .get(`https://api.dexscreener.com/latest/dex/pairs/bsc/${item.pairAddress}`)
        .then((res) => res)
        .catch((err) => null);
 

        netInvestment+= item.investment;   
        const qtty = item.quantity;  
        if(item.sellAtTime === null){
          openTrades++;
          item.profit='Not Sold'
        } else {
          closedTrades++;
           
          totalProfit+= item.profit*item.investment/100;

        } 
        item.quantity = qtty.toFixed(2);

      }); 
      setTotalProfit(totalProfit); 
      setAllTrades(trades); 
     })
  }

  const deleteTrade =(id)=>{
    axios.get('/deleteTrade/'+id).then((res)=>{ 
         refreshData(); 
    })
  }
  useEffect(() => {
    var t = setInterval(refreshData(),5000);
  },[false])

  

  return (
    <div>
      <AppHeader links={NavItems.userLinks} />
      <Container size={'lg'} px={0}>
        <div className={classes.wrapper}>
          <Grid mb={5}>
            <Col span={12} md={12}>
              <Title className={classes.title} order={2} style={{color:'cyan'}}>
                DexVader  <span style={{ fontWeight: 'normal', fontSize: '16px' ,color:'white'}}>
                  <br />A Passive Income Bot to trade regularly on Dex to earn profits.</span>
              </Title>
            </Col>
          </Grid> 
          <TopStats />
          <Card radius={0}>
            <Card.Section withBorder>
             <LoadingOverlay visible={visible} overlayBlur={2} />
             {allTrades && <ScrollArea>
                <Table sx={{ minWidth: 960 }} verticalSpacing="md">
                  <thead>
                    <tr>
                      <th>Symbol</th>
                      <th>BuyAt</th>
                      <th>SellAt</th>
                      <th>Investment</th>
                      <th>Quantity</th>
                      <th>Signalled On</th>
                      <th>Profit/Loss</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>{getTradeRows(allTrades)}</tbody>
                </Table>
              </ScrollArea>}
            </Card.Section>

          </Card>
        </div>
      </Container>
    </div>
  );
}

export default App;
